"use client";

import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { collection, getDocs, addDoc, query, where } from "firebase/firestore";
import { createUserWithEmailAndPassword } from "firebase/auth";
import { auth, db } from "@/lib/firebase";
import { useAuth } from "@/contexts/AuthContext";

interface User {
  id: string;
  nome: string;
  email: string;
  cargo: string;
  departamento: string;
  empresa: string;
  status: "ativo" | "inativo";
}

interface Company {
  id: string;
  razaoSocial: string;
}

export default function UsuariosPage() {
  const [users, setUsers] = useState<User[]>([]);
  const [companies, setCompanies] = useState<Company[]>([]);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const { user: currentUser } = useAuth();
  const [newUser, setNewUser] = useState({
    nome: "",
    email: "",
    senha: "",
    cargo: "",
    departamento: "",
    empresa: "",
  });

  const isSuperAdmin = currentUser?.email === "goes95@gmail.com";

  useEffect(() => {
    const fetchData = async () => {
      try {
        // Fetch companies first
        const companiesSnap = await getDocs(collection(db, "companies"));
        const companiesData = companiesSnap.docs.map(doc => ({
          id: doc.id,
          razaoSocial: doc.data().razaoSocial,
        }));
        setCompanies(companiesData);

        // Then fetch users based on permissions
        let usersQuery;
        if (isSuperAdmin) {
          usersQuery = collection(db, "Users");
        } else {
          // Regular admin only sees users from their company
          usersQuery = query(
            collection(db, "Users"),
            where("empresa", "==", currentUser?.uid)
          );
        }

        const usersSnap = await getDocs(usersQuery);
        const usersData = usersSnap.docs.map(doc => ({
          id: doc.id,
          ...doc.data(),
        })) as User[];
        setUsers(usersData);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchData();
  }, [currentUser, isSuperAdmin]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      // Create authentication user
      const userCredential = await createUserWithEmailAndPassword(
        auth,
        newUser.email,
        newUser.senha
      );

      // Create user document
      const docRef = await addDoc(collection(db, "Users"), {
        uid: userCredential.user.uid,
        nome: newUser.nome,
        email: newUser.email,
        cargo: newUser.cargo,
        departamento: newUser.departamento,
        empresa: newUser.empresa,
        status: "ativo",
      });

      setUsers([...users, {
        id: docRef.id,
        nome: newUser.nome,
        email: newUser.email,
        cargo: newUser.cargo,
        departamento: newUser.departamento,
        empresa: newUser.empresa,
        status: "ativo",
      }]);

      setNewUser({
        nome: "",
        email: "",
        senha: "",
        cargo: "",
        departamento: "",
        empresa: "",
      });
      setIsDialogOpen(false);
    } catch (error) {
      console.error("Error adding user:", error);
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold">Usuários</h1>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button className="bg-black text-white hover:bg-gray-800">
              + Novo Usuário
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Novo Usuário</DialogTitle>
            </DialogHeader>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <Input
                  placeholder="Nome"
                  value={newUser.nome}
                  onChange={(e) => setNewUser({
                    ...newUser,
                    nome: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  type="email"
                  placeholder="E-mail"
                  value={newUser.email}
                  onChange={(e) => setNewUser({
                    ...newUser,
                    email: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  type="password"
                  placeholder="Senha"
                  value={newUser.senha}
                  onChange={(e) => setNewUser({
                    ...newUser,
                    senha: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  placeholder="Cargo"
                  value={newUser.cargo}
                  onChange={(e) => setNewUser({
                    ...newUser,
                    cargo: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  placeholder="Departamento"
                  value={newUser.departamento}
                  onChange={(e) => setNewUser({
                    ...newUser,
                    departamento: e.target.value,
                  })}
                  required
                />
              </div>
              {isSuperAdmin && (
                <div>
                  <Select
                    value={newUser.empresa}
                    onValueChange={(value) => setNewUser({
                      ...newUser,
                      empresa: value,
                    })}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Empresa" />
                    </SelectTrigger>
                    <SelectContent>
                      {companies.map((company) => (
                        <SelectItem key={company.id} value={company.id}>
                          {company.razaoSocial}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              )}
              <Button type="submit" className="w-full bg-black text-white hover:bg-gray-800">
                Salvar
              </Button>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      <div className="grid grid-cols-2 gap-6">
        {users.map((user) => (
          <div
            key={user.id}
            className="bg-white rounded-lg shadow p-6"
          >
            <div className="flex justify-between items-start">
              <div className="flex items-start space-x-4">
                <div className="h-10 w-10 rounded-full bg-gray-200 flex items-center justify-center">
                  <span className="text-lg font-medium text-gray-600">
                    {user.nome.charAt(0).toUpperCase()}
                  </span>
                </div>
                <div>
                  <h3 className="font-medium">{user.nome}</h3>
                  <p className="text-sm text-gray-500">
                    {user.email}
                  </p>
                  <p className="text-sm text-gray-500">
                    Cargo: {user.cargo}
                  </p>
                  <p className="text-sm text-gray-500">
                    Departamento: {user.departamento}
                  </p>
                </div>
              </div>

              <div className="flex items-center space-x-2">
                <span className={`px-3 py-1 rounded-full text-sm ${
                  user.status === "ativo"
                    ? "bg-green-100 text-green-800"
                    : "bg-red-100 text-red-800"
                }`}>
                  {user.status === "ativo" ? "Ativo" : "Inativo"}
                </span>
                <Button variant="ghost" size="sm">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="20"
                    height="20"
                    viewBox="0 0 24 24"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  >
                    <path d="M17 3a2.85 2.83 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3Z" />
                  </svg>
                </Button>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
